<template>
   <div class="hello">
        <h1 style="color: cornflowerblue">iContinu Facial Analysis</h1>
        <h2 style="color: cornflowerblue" font-size="15px">Analyze the features of different images which contains faces and come out if they are matched.</h2>
        <br>
          <img :src='avatar' id="img" width="40%" height="40%" @change="changeImage">
        <br><br>
        <video id="video" width="480" height="320" autoplay></video>
        <canvas id="canvas" width="480" height="320" style="display: none;"></canvas>
        <div>
          <button id="capture" title="click to capture" @click="takePhoto">Take</button>
        </div>
        <br>

        Please select an images(jpeg, jpg, svg, png).
        <br><br>
        <div>
          <input type="file" name="avatar" id="uppic" @change="changeImage($event)">
        </div>
        <br>
        <input id="checkbox" type="checkbox" v-on:click="ifchecked($event)">I agree with the <a href="privacy" target="_blank">privacy policy</a>.
        <br><br>
        <button id="submit" type="submit" form="form" value="Submit" v-bind:disabled="true" >Submit</button>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      avatar: require('../assets/face1.jpg')
    }
  },
  mounted () {
    this.getCompetence()
  },
  methods: {
    getCompetence () {
      // var canvas = document.getElementById('canvas')
      var video = document.getElementById('video')

      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {}
      }

      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.getUserMedia

          if (!getUserMedia) {
            return
          }

          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject)
          })
        }
      }
      var constraints = {audio: false, video: {width: 480, height: 320, transform: 'scaleX(-1)'}}
      navigator.mediaDevices.getUserMedia(constraints).then(function (stream) {
        // test
        if ('srcObject' in video) {
          video.srcObject = stream
        } else {
          video.src = window.URL.createObjectURL(stream)
        }
        video.onloadmetadata = function (e) {
          video.play()
        }
      })
    },
    takePhoto () {
      const demoFaceId = 'ac11aa20-2f96-47cd-b25b-2e39d952e0a9'
      let canvas = document.getElementById('canvas')
      let context = canvas.getContext('2d')
      let video = document.getElementById('video')
      // draw image on canvas
      context.drawImage(video, 0, 0, 480, 320)
      var image = canvas.toDataURL('image/png')
      this.avatar = image
      console.log('click take photo')
      let file = this.dataURLtoFile(image, 'taken')
      this.axios.post(
        'https://australiaeast.api.cognitive.microsoft.com/face/v1.0/detect?returnFaceId=true',
        file,
        {
          headers: {
            'content-type': 'application/octet-stream',
            'Ocp-Apim-Subscription-Key': 'ab34ba5577ba40c5bf1df4851fb2ded2'
          }
        }
      ).then(response => {
        console.log(response.data[0].faceId)
        this.axios.post(
          'http://127.0.0.1:8000/iContinu/face/',
          {
            faceId_1: demoFaceId,
            faceId_2: response.data[0].faceId
          })
          .then(res => {
            console.log(res)
            console.log(res.data.confidence)
            alert('Confidence is: ' + res.data.confidence.confidence)
          })
      })
    },
    dataURLtoFile (dataURL, filename) {
      var arr = dataURL.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, {type: mime})
    },
    initCamera () {
      let self = this
      navigator.getMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia
      if (navigator.getUserMedia) {
        navigator.getUserMedia({video: {width: 480, height: 320}}, function (stream) {
          var video = document.querySelector('video')
          video.srcObject = stream
          self.streamPicture = stream
          video.onloadmetadata = function (e) {
            video.play()
          }
        }, function (err) {
          console.log('fail:' + err.name)
        })
      } else {
        self.$message.error('not support')
      }
    },
    ifchecked (event) {
      var submit = document.getElementById('submit')
      var checkbox = document.getElementById('checkbox')
      if (checkbox.checked === true) {
        submit.removeAttribute('disabled')
      } else {
        submit.setAttribute('disabled', true)
      }
    },
    changeImage (event) {
      var file = event.target.files[0]
      var reader = new FileReader()
      var that = this
      reader.readAsDataURL(file)
      reader.onload = function (event) {
        that.avatar = this.result
      }
      this.axios.post(
        'https://australiaeast.api.cognitive.microsoft.com/face/v1.0/detect?returnFaceId=true',
        file,
        {
          headers: {
            'content-type': 'application/octet-stream',
            'Ocp-Apim-Subscription-Key': 'ab34ba5577ba40c5bf1df4851fb2ded2'
          }
        }
      ).then(response => {
        console.log(response.data[0].faceId)
      })
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.img-avatar {
  position: absolute;
}
.uppic {
  height: 3rem;
  width: 6rem;
  margin: 0 auto;
  opacity: 0;
  z-index: 99999;
}
.rz-picter {
  height: 3rem;
  width: 6rem;
  margin: 0.3rem auto;
  border: 0.01rem solid #ededed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>



