<template>
    <div id='whole'>
    <div id='left'>
        <div id='BG'>
            <div id='getApp' @mouseover='reverseShow' @mouseout='reverseBack'>
                <img src='../assets/phone.png'>
                <p>Get the App</p>
            </div>
        </div>
        <div id='courses'>
            Upcoming Training<br>
            <hr>
            <img src='../assets/1.png' @mouseover='standUp' @mouseout='sitDown'>
            <img src='../assets/2.png' @mouseover='standUp' @mouseout='sitDown'>
            <img src='../assets/3.png' @mouseover='standUp' @mouseout='sitDown'>
            <img src='../assets/4.png' @mouseover='standUp' @mouseout='sitDown'>
            <img src='../assets/5.png' @mouseover='standUp' @mouseout='sitDown'>
            <img src='../assets/6.png' @mouseover='standUp' @mouseout='sitDown'>
        </div>
    </div>
    <div id='right'>
        <div id='ads'>
            Ads
        </div>
        <div id='survey'>
        Pending Survey<br><hr>
        <div id='surveyList'>
        <li>
            <a>Survey for Professional Development</a><br>
            - INCOMPLETE<br>
            27 FEB
        </li>
        <li>
            <a>Survey for Ethics</a><br>
            - EXPIRED<br>
            13 JAN
        </li>
        </div>
        </div>

    </div>
    </div>
</template>

<script>
export default {
  name: 'HomeBackground',
  methods: {
    standUp (e) {
      e.currentTarget.style.boxShadow = '0px 0px 9px 0px rgba(0,0,0,0.8)'
      e.currentTarget.style.cursor = 'pointer'
    },
    sitDown (e) {
      e.currentTarget.style.boxShadow = '0px 0px 6px 0px rgba(0,0,0,0.3)'
    },
    reverseShow (e) {
      e.currentTarget.style.color = 'white'
      e.currentTarget.style.background = 'orange'
      e.currentTarget.style.cursor = 'pointer'
    },
    reverseBack (e) {
      e.currentTarget.style.color = 'black'
      e.currentTarget.style.background = 'white'
    }
  }
}
</script>

<style scoped>
    #whole{
        font-family: Tahoma;
    }
    #left{
        width: 80%;
        float: left;
        position: relative;
    }
    #BG{
        margin-top: 36px;
        font-size: 20px;
        width: 100%;
        height: 417px;
        float: left;
        border-radius: 8px;
        background: url('../assets/background.png') no-repeat right;
    }
    #getApp{
        width: 166px;
        height: 56px;
        margin-top: 300px;
        margin-left: 60px;
        border-radius: 6px;
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.6);
    }
    #getApp img{
        width: 50px;
        float: left;
        padding-top: 3px;
    }
    #getApp p{
        margin: 0 0 0 0;
        float: left;
        line-height: 56px;
    }
    #courses{
        padding-top: 466px;
        padding-left: 40px;
        color: #5c6ac4;
        font-size: 18px;
    }
    #courses img{
        width: 411px;
        border-radius: 10px;
        margin: 10px 10px 10px 10px;
        box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.3);
    }
    #right {
        font-size: 14px;
        color: #5c6ac4;
        width: 202px;
        padding-left:6px;
        padding-top: 38px;
        float: right;
        margin-right: 20px;
    }
    #ads{
        width: 100%;
        height: 417px;
        text-align: center;
        line-height: 417px;
        border: grey 1px solid;
    }
    #survey{
        margin-top: 16px;
    }
    #surveyList li{
        margin-top: 10px;
    }
    #surveyList{
        color: grey;
    }
    #surveyList a{
        color: darkblue;
    }
</style>



