<template>
    <div id="scan">
        <div id="top">
          <img id="logo" src="../assets/logo1.png">
          <div id="dashtext">
              Dashboard
          </div>
        </div>
        <div id="left">
            <li></li>
            <li></li>
        </div>
        <div style="margin-left: 10%;">
        <br>
        <br>
        <h1 style="color: cornflowerblue">Scan QR Code</h1>
        <br>
        <video id="video" width="640" height="480" muted autoplay></video>
        <canvas id="canvas" width="640" height="480" style="display: none;"></canvas>
        <br><br>
        <br>
      </div>
    </div>
</template>

<script>
export default {
  name: 'scan',
  data () {
    return {
      headImgSrc: ''
    }
  },
  mounted () {
    this.getCompetence()
    this.initScanner()
  },
  methods: {
    getCompetence () {
      // var canvas = document.getElementById('canvas')
      var video = document.getElementById('video')

      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {}
      }

      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.getUserMedia
          if (!getUserMedia) {
            return
          }

          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject)
          })
        }
      }
      var constraints = {audio: false, video: {width: 480, height: 320, transform: 'scaleX(-1)'}}
      navigator.mediaDevices.getUserMedia(constraints).then(function (stream) {
        if ('srcObject' in video) {
          video.srcObject = stream
        } else {
          video.src = window.URL.createObjectURL(stream)
        }
        video.style.MozTransform = 'scaleX(-1)'
        video.style.webkitTransform = 'scaleX(-1)'
        video.style.OTransform = 'scaleX(-1)'
        video.style.msFilter = 'FlipH'
        video.style.filter = 'FlipH'
        video.style.transform = 'scaleX(-1)'
        video.onloadmetadata = function (e) {
          video.play()
        }
      })
    },
    initScanner () {
      const Instascan = require('instascan')
      let self = this
      let scanner = new Instascan.Scanner({
        video: document.getElementById('video')
      })
      scanner.addListener('scan', function (content) {
        alert('get QR Code info: ' + content)
        console.log(content)
        self.$router.push({path: '/verify'})
      })
      Instascan.Camera.getCameras()
        .then(function (cameras) {
          if (cameras.length > 0) {
            console.log(cameras[0].name)
            scanner.start(cameras[0])
          } else {
            console.error('No cameras found.')
          }
        })
        .catch(function (e) {
          console.error(e)
        })
    }
  }
}
</script>

<style scoped>
#sacn{
  min-width: 1000px;
  min-height: 1000px;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

#content{
  background: rgba(255,255,255,0.5);
  width: 100%;
  height: 400px;
  font-size: 26px;
  padding-left: 15px ;
  padding-right: 15px;
  padding-top: 15px;
  margin: 0 auto;
}

#top{
  width: 100%;
  height: 200px;
  min-width: 800px;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  background: url("../assets/page2Bg1.png") no-repeat;
}

#logo{
  padding-top: 10px;
  padding-left: 10px;
  width: 200px;
  position: absolute;
}
#dashtext{
  margin-left: 10%;
  padding-top: 100px;
  font-weight: bold;
  color: orangered;
  font-size: 50px;
  text-align: left;
}
#left{
  width: 170px;
  height: 100%;
  background: antiquewhite;
  position: absolute;
  overflow: hidden;
  border-right: 1px grey solid;
}
#right{
  background: antiquewhite;
  padding-top: 33px;
  padding-left: 43px;
  margin-left: 171px;
  width: 100%;
  height: 100%;
  position: absolute;
  float: left;
}
#photo{
  width: 500px;
  height: 360px;
  border: #232323 1px solid;
}
#submitbtn{
  background-color: rgba(255,192,203,0.4);
  border-radius: 15px;
  height: 30px;
  width: 200px;
  outline: none;
  margin-top: 20px;
}
</style>



