<template>
  <div id='app'>
    <div id='topDock' ref='topTitle'>
      <img id='logo' src='./assets/logo.png'  @mouseover='light'>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>Account</a>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>Suggested</a>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>Report</a>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>History</a>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>Sign Up</a>
      <a @mouseover='toActive' @mouseout='toNormal' @click='sToActive'>Log In</a>
    </div>
    <router-view></router-view>
    <div id='bottom'>
      <hr>
      <a>Privacy Policy</a>
      <a>Facebook</a>
      <a>Linkin</a>
    </div>
    </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      aA: document.getElementsByTagName('a')
    }
  },
  methods: {
    toActive (e) {
      e.currentTarget.style.cursor = 'pointer'
      e.currentTarget.className = 'active'
    },
    toNormal (e) {
      e.currentTarget.className = ''
    },
    sToActive (e) {
      for (let element of this.aA) {
        element.className = ''
      }
      e.currentTarget.className = 'active'
    },
    light (e) {
      e.currentTarget.style.cursor = 'pointer'
    }
  }
}
</script>

<style scoped>
  #app
  {
    font-family: Tahoma;
    width: 100%;
    min-width: 1150px;
    padding: 0;
    margin: 0;
  }
  #topDock{
  min-width: 1150px;
  width: 100%;
  height: 38px;
  position: absolute;
  background-color: #E25A00;
  color: white;
  text-align: center;
  margin: 0 auto;
  z-index: 5;
  }
  #logo{
    width: 66px;
    top: 5px;
    left: 28px;
    position: absolute;
  }
  #topDock a{
    margin-left: 100px;
    font-size: 18px;
    line-height: 38px;
  }
  .active{
      color: khaki;
  }
  #bottom{
    height: 100px;
    color: #5c6ac4;
    font-size: 20px;
    margin-left: 36px;
    text-align: right;
    width: calc(100% - 56px);
    float: left;
  }
  #bottom a{
    margin-left: 20px;
    margin-right: 6px;
  }
</style>



