#login{
  background-repeat: no-repeat;
  background-image: url("../assets/background.jpg");
  width: 100%;
  height: 100%;
  min-height: 1000px;
  min-width: 1000px;
  position: absolute;
  padding: 0;
  margin: 0;
  left: 0;
